<!--  -->
<template>
  <div class="">
    <el-table
      :data="hostData"
      border
      style="width: 100%"
      :header-cell-style="{
        'background-color': 'rgba(32, 39, 65, 0.7)',
        color: '#fff',
      }"
    >
      <el-table-column prop="mac" label="mac地址"> </el-table-column>
      <el-table-column prop="ipv4" label="ipv4"> </el-table-column>
      <el-table-column prop="ipv6" label="ipv6"> </el-table-column>
      <el-table-column label="操作">
        <template slot-scope="scope">
          <el-button type="text" @click="test(scope.$index, scope.row)"
            >点击查看详细信息</el-button
          >

          <el-dialog
            title="端口详细信息"
            :visible.sync="dialogVisible"
            width="30%"
          >
            <el-descriptions>
              <el-descriptions-item label="dpid">{{
                port.dpid
              }}</el-descriptions-item>
              <el-descriptions-item label="port_on">{{
                port.port_no
              }}</el-descriptions-item>
              <el-descriptions-item label="hw_addr">{{
                port.hw_addr
              }}</el-descriptions-item>
              <el-descriptions-item label="name">
                <el-tag size="medium">{{ port.name }}</el-tag>
              </el-descriptions-item>
            </el-descriptions>
          </el-dialog>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
//这里可以导入其他文件（比如：组件，工具js，第三方插件js，json文件，图片文件等等）
//例如：import 《组件名称》 from '《组件路径》';

export default {
  //import引入的组件需要注入到对象中才能使用
  components: {},
  data() {
    //这里存放数据
    return {
      hostData: [],
      dialogVisible: false,
      port: [],
    };
  },
  //监听属性 类似于data概念
  computed: {},
  //监控data中的数据变化
  watch: {},
  //方法集合
  methods: {
    loadHosts() {
      this.$axios
        .get(this.$httpUrl + "/inf/hostsInf")
        .then((res) => res.data)
        .then((res) => {
          console.log(res);
          this.hostData = res;
        });
    },
    handleClose(done) {
      this.$confirm("确认关闭？")
        // eslint-disable-next-line
        .then((_) => {
          done();
        })
        // eslint-disable-next-line
        .catch((_) => {});
    },
    test(a, b) {
      console.log(a);
      console.log(b);
      this.port = b.port;
      this.dialogVisible = true;
    },
  },
  //生命周期 - 创建完成（可以访问当前this实例）
  created() {},
  //生命周期 - 挂载完成（可以访问DOM元素）
  mounted() {
    this.loadHosts();
  },
  beforeCreate() {}, //生命周期 - 创建之前
  beforeMount() {}, //生命周期 - 挂载之前
  beforeUpdate() {}, //生命周期 - 更新之前
  updated() {}, //生命周期 - 更新之后
  beforeDestroy() {}, //生命周期 - 销毁之前
  destroyed() {}, //生命周期 - 销毁完成
  activated() {}, //如果页面有keep-alive缓存功能，这个函数会触发
};
</script>
<style lang='scss' scoped>
//@import url(); 引入公共css类
</style>